import Aos from "aos";
import "aos/dist/aos.css";
import { useTranslation } from "react-i18next";

const Content = ({
  imageL = null,
  altL = "",
  firstParagraph = "",
  secondParagraph = "",
  thirdParagraph = "",
  judge = false,
}) => {
  const { t } = useTranslation();

  Aos.init({
    offset: 100,
    delay: 50,
    once: true,
  });

  return (
    <div className="content">
      <img
        className="contentLeft-img"
        src={imageL}
        alt={altL}
        data-aos="fade-up"
        data-aos-duration="500"
      />
      <div
        className="milestone"
        style={{ display: `${judge === false ? "none" : "inline-block"}` }}
        data-aos="fade-up"
        data-aos-duration="500"
      >
        <table>
          <tbody>
            <tr>
              <th>2002</th>
              <td>{t("")}</td>
            </tr>
            <tr>
              <th>2007</th>
              <td>{t(`ISO90012008`)}</td>
            </tr>
            <tr>
              <th>2008</th>
              <td>{t("TUV")}</td>
            </tr>
            <tr>
              <th>2009</th>
              <td>{t("HVDC")}</td>
            </tr>
            <tr>
              <th>2013</th>
              <td>{t("DENSO")}</td>
            </tr>
            <tr>
              <th>2016</th>
              <td>{t("")}</td>
            </tr>
            <tr>
              <th rowSpan={2}>2017</th>
              <td>{t("")}</td>
            </tr>
            <tr>
              <td>{t("ISO90012015")}</td>
            </tr>
            <tr>
              <th>2018</th>
              <td>{t("")}</td>
            </tr>
            <tr>
              <th>2019</th>
              <td>{t("")}</td>
            </tr>
            <tr>
              <th rowSpan={2}>2020</th>
              <td>{t("")}</td>
            </tr>
            <tr>
              <td>{t("IATF16949")}</td>
            </tr>
            <tr>
              <th>2022</th>
              <td>{t("")}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div className="content-txt" data-aos="fade-up" data-aos-duration="500">
        <p>{t(`${firstParagraph}`)}</p>
        <p>{t(`${secondParagraph}`)}</p>
        <p>{t(`${thirdParagraph}`)}</p>
      </div>
    </div>
  );
};

export default Content;
